.projects {
  padding: 60px 20px;
  background-color: #f9f9f9;
  text-align: center;
}

.projects h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  font-family: 'Poppins', sans-serif;
  color: #333;
}

.project-carousel {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.project-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between; /* Distributes space between elements */
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  border-radius: 15px;
  padding: 20px;
  width: 300px; /* Fixed width */
  height: 350px; /* Fixed height */
  box-shadow: 8px 8px 15px #d1d1d1, -8px -8px 15px #ffffff;
  transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif;
  text-decoration: none; /* Remove underline from links */
  color: inherit; /* Ensure text color inherits from parent */
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 8px 8px 20px #c4c4c4, -8px -8px 20px #ffffff;
}

.project-card h3 {
  font-size: 1.25rem;
  color: #555;
  margin: 0;
  text-align: center;
}

.project-card p {
  font-size: 0.875rem;
  color: #777;
  margin-top: 10px;
  text-align: center;
  flex-grow: 1; /* Pushes content down */
}

@media (max-width: 768px) {
  .project-card {
    width: 100%;
    height: auto; /* Adjust height for smaller screens */
    padding: 15px;
  }
}
