.achievements {
  padding: 60px 20px;
  background-color: #f9f9f9;
  text-align: center;
}

.achievements h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  font-family: 'Poppins', sans-serif;
  color: #333;
}

.achievement-carousel {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.achievement-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  border-radius: 15px;
  padding: 20px;
  width: 300px;
  box-shadow: 8px 8px 15px #d1d1d1, -8px -8px 15px #ffffff;
  transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif;
}

.achievement-card:hover {
  transform: translateY(-5px);
  box-shadow: 8px 8px 20px #c4c4c4, -8px -8px 20px #ffffff;
}

.achievement-image {
  width: 100%;
  height: 150px; /* Adjust the height as needed */
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 15px;
}

.achievement-text {
  text-align: center;
}

.achievement-card h3 {
  font-size: 1.25rem;
  color: #555;
}

.achievement-card p {
  font-size: 0.875rem;
  color: #777;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .achievement-card {
    width: 100%;
    padding: 15px;
  }
}
