.about-section {
  padding: 60px 20px;
  background: #f9f9f9; /* Light background color */
  display: flex;
  justify-content: center; /* Center the content horizontally */
  padding-top: 150px;
  align-items: center; /* Center the content vertically */
  font-family: 'Poppins', sans-serif; /* Set font family */
  flex-direction: column; /* Stack items vertically on small screens */
}

.about-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-width: 1200px; /* Set a max width for better alignment */
  width: 100%;
  flex-wrap: wrap; /* Allow items to wrap to the next line */
  gap: 20px; /* Add space between items */
}

.about-info {
  max-width: 600px; /* Ensure that the text content doesn't exceed half of the section */
  padding-left: 20px; /* Adjust padding for smaller screens */
  padding-top: 0px; /* Padding above the content */
  text-align: left; /* Left-align text */
}

.about-info h2 {
  font-size: 1.5rem; /* Adjust font size for smaller screens */
  color: #000; /* Black text color */
  margin-bottom: 10px;
}

.intro-text {
  font-size: 1rem; /* Adjust font size for smaller screens */
  color: #000; /* Black text color */
  margin-bottom: 20px;
}

.social-media {
  display: flex;
  justify-content: flex-start; /* Align icons to the left */
  gap: 15px;
  margin-bottom: 20px;
}

.social-media a {
  color: #000; /* Black icon color */
  font-size: 1.2rem; /* Adjust font size for smaller screens */
}

.location {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-bottom: 20px;
}

.location span {
  font-size: 0.9rem; /* Adjust font size for smaller screens */
  color: #000; /* Black text color */
}

.languages {
  font-size: 0.9rem; /* Adjust font size for smaller screens */
  color: #000; /* Black text color */
}

.about-image {
  flex: 1; /* Take up remaining space */
  text-align: center; /* Center the image */
  padding-top: 20px;
  padding-left: 20px; /* Adjust padding for smaller screens */
}

.about-image img {
  max-width: 100%;
  height: auto;
  border-radius: 50%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.about-image img:hover {
  transform: scale(1.05);
}

/* Media Queries for mobile devices */
@media (max-width: 768px) {
  .about-content {
    flex-direction: column; /* Stack content vertically */
    align-items: center; /* Center align items */
  }

  .about-info {
    padding-left: 0; /* Remove left padding on smaller screens */
    text-align: center; /* Center align text */
  }

  .about-image {
    padding-left: 0; /* Remove left padding on smaller screens */
  }
}
