.contact {
  padding: 60px 20px;
  background-color: #000; /* Black background for the contact section */
  color: #fff; /* White text color */
  text-align: center;
}

.contact h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  font-family: 'Poppins', sans-serif;
  color: #fff; /* White text color */
}

.contact .contact-card h3 {
  color: #fff; /* White text color for the section headings */
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Two columns layout */
  gap: 20px;
  justify-content: center;
}

.contact-card {
  background: linear-gradient(
    145deg,
    #1e1e1e,
    #333
  ); /* Darker gradient for black theme */
  border-radius: 15px;
  padding: 20px;
  box-shadow: 8px 8px 15px #111, -8px -8px 15px #444; /* Darker shadows */
  transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif;
}

.contact-card:hover {
  transform: translateY(-5px);
  box-shadow: 8px 8px 20px #000, -8px -8px 20px #333; /* Darker shadows on hover */
}

.contact-form,
.resume-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.contact-form label,
.resume-form label {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.contact-form input,
.resume-form input,
.contact-form textarea {
  padding: 10px;
  border: 1px solid #fff; /* White border for textboxes */
  border-radius: 5px;
  background-color: #fff; /* White background for textboxes */
  color: #000; /* Black text color inside textboxes */
  font-size: 1rem;
}

.contact-form button,
.resume-form button {
  background-color: #000; /* Black background for buttons */
  color: #fff; /* White text color for buttons */
  border: 1px solid #fff; /* White border for buttons */
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.contact-form button:hover,
.resume-form button:hover {
  background-color: #333; /* Darker black on hover for buttons */
}
