/* src/App.css */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  line-height: 1.6;
}

.container {
  padding: 20px;
}

.section {
  padding: 40px 20px;
  border-bottom: 1px solid #ddd;
}

h1,
h2,
h3 {
  color: #333;
}

h2 {
  border-bottom: 2px solid #ddd;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

a {
  color: #1e90ff;
}

a:hover {
  text-decoration: underline;
}
