.activities {
  padding: 60px 20px;
  background-color: #f4f4f4;
  text-align: center;
}

.activities h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  font-family: 'Poppins', sans-serif;
  color: #333;
}

.activities-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-content: center;
  padding: 0 20px;
}

.activity-card {
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 8px 8px 15px #d1d1d1, -8px -8px 15px #ffffff;
  transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif;
}

.activity-card:hover {
  transform: translateY(-5px);
  box-shadow: 8px 8px 20px #c4c4c4, -8px -8px 20px #ffffff;
}

.activity-card h3 {
  font-size: 1.25rem;
  color: #555;
}

.activity-card h4 {
  font-size: 1rem;
  color: #888;
  margin-bottom: 10px;
}

.activity-card p {
  font-size: 0.875rem;
  color: #777;
}

@media (max-width: 768px) {
  .activities-container {
    grid-template-columns: 1fr;
  }
}
