.skills-section {
  padding: 60px 20px;
  background-color: #f9f9f9;
  text-align: center;
}

.skills-section h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  font-family: 'Poppins', sans-serif;
  color: #333;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
}

.skill-box {
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  border-radius: 15px;
  padding: 20px;
  width: 120px; /* Adjusted to ensure uniform size */
  height: 120px; /* Same as width for square shape */
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 8px 8px 15px #d1d1d1, -8px -8px 15px #ffffff;
  transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif;
}

.skill-box:hover {
  transform: translateY(-5px);
  box-shadow: 8px 8px 20px #c4c4c4, -8px -8px 20px #ffffff;
}

.skill-name {
  font-size: 0.875rem;
  font-weight: 600;
  color: #555;
  margin: 0;
}

.skill-name::after {
  content: '';
  display: block;
  margin: 8px auto;
  width: 40px;
  height: 2px;
  background-color: #333;
  opacity: 0.1;
}

@media (max-width: 768px) {
  .skill-box {
    width: 100px; /* Adjusted for mobile view */
    height: 100px;
    padding: 15px;
  }

  .skill-name {
    font-size: 0.75rem;
  }
}
