.education {
  padding: 60px 20px;
  background-color: #f9f9f9;
  text-align: center;
}

.education h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  font-family: 'Poppins', sans-serif;
  color: #333;
}

.education-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.education-card {
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  border-radius: 15px;
  padding: 20px;
  width: 350px;
  box-shadow: 8px 8px 15px #d1d1d1, -8px -8px 15px #ffffff;
  transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif;
}

.education-card:hover {
  transform: translateY(-5px);
  box-shadow: 8px 8px 20px #c4c4c4, -8px -8px 20px #ffffff;
}

.education-card h3 {
  font-size: 1.25rem;
  color: #555;
}

.education-card h4 {
  font-size: 1rem;
  color: #888;
  margin-bottom: 10px;
}

.education-card p {
  font-size: 0.875rem;
  color: #777;
}

@media (max-width: 768px) {
  .education-card {
    width: 100%;
    padding: 15px;
  }
}
